html, body, #content_wrapper {
  height: 100%;
  width: 100%;
  -webkit-font-smoothing: antialiased;
}

html {
  //overflow-y: scroll;
  min-width: 1440px;
  max-width: 1920px;
  //min-height: 768px;
  background-color: #ebebeb;
}

body {
  display: block;
  position: relative;
}

html, body, div, header,
nav, aside, article, section,
footer, p, span, h1,
h2, h3, h4, h5, h6,
figure, figcaption, fieldset,
input, button, ul, li,
label {
  margin: 0;
  padding: 0;
  outline: none;
  border: 0;
  vertical-align: baseline;
  font-size: 100%;
  font-weight: 400;
}

a, a:visited, a:active {
  text-decoration: none;
  outline: none;
  color: inherit;
}

article, aside, details, figcaption, figure, footer, header, menu, nav, section {
  display: block;
}

#main_header {
  position: relative;
  width: 100%;
  height: 95px;
  background-color: #fff;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: normal;
  font-style: normal;
  margin-bottom: 60px;
  img {
    float: left;
    padding: 5px 30px;
  }

  a#user_avatar {
    position: absolute;
    top: 10px;
    right: 30px;
    width: 75px;
    height: 75px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    img {
      margin: 0;
      padding: 0;
      width: 75px;
      height: 75px;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      border-radius: 50%;
    }
  }
}

#main_footer {

}

#main_menu {
  overflow: hidden;
}

#main_menu > a,
.subnav > a {
  display: block;
  padding: 0px;
  float: left;
  color: #2b2b2b;
  margin: 35px 10px;
  font-size: 25px;
  line-height: 25px;
}

#main_menu a:hover,
#main_menu a.active,
.subnav:hover > a {
  color: #eb5252;
}

.subnav {
  float: left;
  overflow: hidden;
  padding-bottom: 10px;
}

.subnav-content {
  display: none;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 105px;
  width: 100%;
  background-color: #fff;
  padding: 10px 20px;
  z-index: 1;
  transition: visibility 0.5s, opacity 0.5s linear;
}
.subnav .active {
  display: block;
  opacity: 1;
}

.subnav-content a {
  display: block;
  margin: 0 10px;
  padding: 0;
  float: left;
  color: #2b2b2b;
  font-size: 20px;
  line-height: 20px;
}

.subnav:hover .subnav-content,
.subnav-content:hover {
  display: block;
  opacity: 1;
  visibility: visible;
  transition: visibility 0.5s, opacity 0.5s linear;
}

#side_menu {
  position: absolute;
  width: 420px;
  background-color: #fff;
  margin-top: 20px;
  float: left;
}

#main_content {
  width: 100%;
  background-color: #fff;
  margin-top: 20px;
  margin-left: 450px;
  float: left;
}